(function(F) {
	'use strict';

	var getParamByName = function(name) {
		// Get the href
		var url = window.location.href;

		// Set up regex
		var regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)');

		// Get the regex results
		var results = regex.exec(url);

		// If no results return null
		if (! results) {
			return null;
		}

		// If no results return empty string
		if (! results[2]) {
			return '';
		}

		// Return the results
		return decodeURIComponent(results[2].replace(/\+/g, ' '));
	};

	F.controller.make('ContactForm', {
		/**
		 * Submit button
		 */
		$submitBtn: $(),

		/**
		 * Submit button text
		 */
		submitBtnText: null,

		/**
		 * Submit button working text
		 */
		submitBtnWorkingText: null,

		/**
		 * Submit button working text
		 */
		submitBtnSuccessText: null,

		/**
		 * Events
		 */
		events: {
			'submit': function(e) {
				// Prevent HTML submit
				e.preventDefault();

				// Run the onSubmit function
				this.onSubmit();
			},
			'change .js-input ': function(e) {
				this.removeInputError(e.currentTarget);
			}
		},

		/**
		 * Init function runs on controller construct
		 */
		init: function() {
			// Save a reference to the controller
			var self = this;

			// Get the submit button
			self.$submitBtn = self.$el.find('.js-submit-form');

			// Get the submit button text
			self.submitBtnText = self.$submitBtn.val();

			// Get the submit button text
			self.submitBtnWorkingText = self.$submitBtn.data('workingText');

			// Get the submit success text
			self.submitBtnSuccessText = self.$submitBtn.data('successText');

			// Set the subject if needed
			self.setSubject();
		},

		/**
		 * Set subject
		 */
		setSubject: function() {
			// Save a reference to the controller
			var self = this;

			// Get the subject
			var subject = getParamByName('subject');

			// Set the subject if exists
			if (subject) {
				self.$el.find('[name="subject"]').val(subject);
			}
		},

		/**
		 * Disable submit button
		 */
		disableSubmit: function() {
			// Save a reference to the controller
			var self = this;

			// Disable the button and add the working text
			self.$submitBtn.attr('disabled', true)
				.val(self.submitBtnWorkingText);
		},

		/**
		 * Enable submit button
		 */
		enableSubmit: function() {
			// Save a reference to the controller
			var self = this;

			// Enable the submit button and add the standard text back
			self.$submitBtn.attr('disabled', false)
				.val(self.submitBtnText);
		},

		/**
		 * On form submit
		 */
		onSubmit: function() {
			// Save a reference to the controller
			var self = this;

			// Disable the submit button
			self.disableSubmit();

			// Submit the form
			$.ajax({
				url: self.$el.data('actionUrl'),
				method: 'POST',
				data: this.$el.serialize(),
				success: function(resp) {
					if (resp.success) {
						self.onSuccess();
					} else {
						self.onError(resp.error);
					}
				}
			});
		},

		/**
		 * On form submission success
		 */
		onSuccess: function() {
			// Save a reference to the controller
			var self = this;

			// Disable all inputs
			self.$el.find('.js-input').attr('disabled', true);

			// Display the success message
			self.$el.find('.js-contact-success').slideDown(200);

			// Set the success text on the submit button
			self.$submitBtn.val(self.submitBtnSuccessText);
		},

		/**
		 * On form submission error
		 *
		 * @param {Object} resp
		 */
		onError: function(resp) {
			// Save a reference to the controller
			var self = this;

			// Enable the submit button
			self.enableSubmit();

			// Loop through response
			for (var i in resp) {
				// Process input
				self.inputErrorProcess(i, resp[i]);
			}
		},

		/**
		 * Input error process
		 *
		 * @param {String} name
		 * @param {Object} obj
		 */
		inputErrorProcess: function(name, obj) {
			// Save a reference to the controller
			var self = this;

			// Get the input
			var $input = this.$el.find('[name="' + name + '"]');

			// Get the input error class
			var errorClass = $input.data('errorClass');

			// Remove input errors
			self.removeInputError($input);

			// Remove the error class in case it exists
			$input.removeClass(errorClass);

			// Check if error is true
			if (obj.error) {
				// Add the error class
				$input.addClass(errorClass);

				// Insert the error
				$input.after('<div class="contact-form__input-errors js-input-error">' + obj.errorMessage + '</div>');
			}
		},

		/**
		 * Input change
		 *
		 * @param {HTMLElement|jQuery} input
		 */
		removeInputError: function(input) {
			var $input = $(input);

			// Get the input error class
			var errorClass = $input.data('errorClass');

			// Look for input error message
			var $inputErrorMessage = $input.siblings('.js-input-error');

			// Remove error message if present
			if ($inputErrorMessage.length) {
				$inputErrorMessage.remove();
			}

			// Remove the error class in case it exists
			$input.removeClass(errorClass);
		}
	});
})(window.FAB, window.Craft);
