(function(F, W) {
	'use strict';

	F.controller.make('Pjax', {
		/**
		 * Element
		 */
		el: 'body',

		/**
		 * Html Body
		 */
		$htmlBody: $('html, body'),

		/**
		 * Html Title
		 */
		$htmlTitle: $('title'),

		/**
		 * PJAX Loader element
		 */
		$pjaxLoader: $('<div class="pjax-loader js-hide"></div>'),

		/**
		 * Wrap all element
		 */
		$wrapAll: $('.js-site-wrapper'),

		/**
		 * Model
		 */
		model: {
			uriPath: 'string',
			newTabKeyDown: 'bool'
		},

		/**
		 * Events
		 */
		events: {
			'click a:not([href^="#"])': function(e) {
				// Save a reference to the controller
				var self = this;

				// Get the link
				var $el = $(e.currentTarget);

				// Get the href
				var href = $el.attr('href');

				// Set href array
				var hrefArray = href.split('/');

				// Make sure the new tab key is not down
				if (self.model.get('newTabKeyDown')) {
					return;
				}

				// Make sure URL is not external
				if (! self.isInternalUrl(href)) {
					return;
				}

				// Check if this is a feed link
				if (
					hrefArray[hrefArray.length - 1] === 'feed' ||
					hrefArray[hrefArray.length - 1] === 'masterfeed'
				) {
					return;
				}

				// Prevent link from loading so we can do PJAX
				e.preventDefault();

				// Update the global state
				self.historyPush(href);
			},
			'click a[href^="#"]': function(e) {
				e.preventDefault();
			},
			keydown: function(e) {
				// Save a reference to the controller
				var self = this;

				// Check if the new tab key is pressed
				if (e.keyCode === 91) {
					self.model.set('newTabKeyDown', true);
				}
			},
			keyup: function(e) {
				// Save a reference to the controller
				var self = this;

				// Check if the new tab key is back up
				if (e.keyCode === 91) {
					self.model.set('newTabKeyDown', true);
				}
			}
		},

		/**
		 * Init function runs on controller construct
		 */
		init: function() {
			// Save a reference to the controller
			var self = this;

			// Insert the PJAX loader element
			self.$el.append(self.$pjaxLoader);

			// In the future we might want to call a runOnPageLoad function here

			// Watch for changes to uriPath and request the page when changed
			self.model.onChange('uriPath', function(val) {
				self.requestPage(val);
			});

			// Watch the popstate
			W.addEventListener('popstate', function() {
				self.model.set(
					'uriPath',
					W.location.pathname + W.location.search
				);
			});

			// Watch for pageRendered
			F.globalState.onChange('pageRendered', function() {
				self.postRender();
			});
		},

		// Check if url is external
		isInternalUrl: function(href) {
			var domain = function(href) {
				return href.replace('http://', '')
					.replace('https://', '')
					.replace('//', '')
					.split('/')[0];
			};

			return domain(W.location.href) === domain(href) || ! domain(href);
		},

		/**
		 * Push history state
		 *
		 * @param {String} uri
		 */
		historyPush: function(uri) {
			// Save a reference to the controller
			var self = this;

			self.model.set('uriPath', uri);

			// Update the history state
			if (W.location.pathname + W.location.search !== uri) {
				history.pushState(null, null, uri);
			}
		},

		/**
		 * Request page
		 *
		 * @param {String} href
		 */
		requestPage: function(href) {
			// Save a reference to the controller
			var self = this;

			// Show the PJAX loader
			self.$pjaxLoader.jsShow().css('opacity', '1');

			// Initial PJAX loader animation
			self.$pjaxLoader.animate({
				width: '20%'
			}, 200);

			// Run request
			$.ajax({
				url: href,
				headers: {
					'X-PJAX': 'true'
				},
				success: function(html) {
					// Render the page
					self.render(html);
				},
				error: function(xhr) {
					if (xhr.status === 404) {
						// Render the page
						self.render(xhr.responseText);
					}
				}
			});
		},

		/**
		 * Render
		 *
		 * @param {String} html
		 */
		render: function(html) {
			// Save a reference to the controller
			var self = this;

			// Cast the html
			var $html = $('<div>' + html + '</div>');

			// Replace the page title
			self.$htmlTitle.html($html.find('title').html());

			// Scroll to the top of the page
			self.$htmlBody.animate({
				scrollTop: 0
			}, 400);

			// Fade out the old content
			self.$wrapAll.fadeOut(100, function() {
				// Place the new content on the DOM
				self.$wrapAll.html($html.find('.js-site-wrapper').html());

				// Animate the PJAX loader all the way across
				self.$pjaxLoader.animate({
					width: '100%'

					// When the animation is finished
				}, 200, function() {
					// Fade out the PJAX loader
					self.$pjaxLoader.animate({
						opacity: '0'

						// When the animation is finished
					}, 400, function() {
						// Remove the PJAX loader
						self.$pjaxLoader.jsHide().css('width', '0%');
					});
				});

				// Fade in the new content
				self.$wrapAll.fadeIn(100, function() {
					// Increment rendered state to trigger subscribed changes
					F.globalState.set(
						'pageRendered',
						F.globalState.get('pageRendered') + 1
					);
				});
			});
		},

		/**
		 * Post render
		 */
		postRender: function() {
			if (typeof ga !== 'undefined') { // jshint ignore:line
				ga('send', 'pageview', W.location.pathname); // jshint ignore:line
			}

			F.runOnPageRendered();
		}
	});
})(window.FAB, window);
