(function(F) {
	'use strict';

	// Get the audio player template
	var audioPlayerTemplate = $('#jsTemplate__audioPlayer').html();

	// Global audio model
	var GlobalAudioModelConstructor = F.model.make({
		playAction: 'int'
	});
	var globalAudioModel = new GlobalAudioModelConstructor();

	F.controller.make('AudioPlayer', {
		model: {
			isMini: 'bool',
			isPlaying: 'bool',
			duration: 'string'
		},

		/**
		 * jQuery cast template
		 */
		$audioPlayerTemplate: null,

		/**
		 * Play/pause button
		 */
		$playPause: null,

		/**
		 * Played Progress bar
		 */
		$barPlayed: null,

		/**
		 * Bar scrubber
		 */
		$barScrubber: null,

		/**
		 * Current Time element
		 */
		$currentTime: null,

		/**
		 * Roll back element
		 */
		$rollBack: null,

		/**
		 * Roll forward element
		 */
		$rollForward: null,

		/**
		 * Roll forward element
		 */
		$playerSpeed: null,

		/**
		 * Scrub Area
		 */
		$scrubArea: null,

		/**
		 * Scrub Area
		 */
		$scrubHover: null,

		/**
		 * Mini class
		 */
		miniClass: 'audio-player--mini',

		/**
		 * Mini class
		 */
		playStoppedClass: 'audio-player__play-pause-button--stopped',

		/**
		 * Mini class
		 */
		playPlayingClass: 'audio-player__play-pause-button--playing',

		/**
		 * Init runs on controller construction
		 */
		init: function() {
			// Save a reference to the controller
			var self = this;

			// Set the $audioPlayerTemplate
			self.$audioPlayerTemplate = $(audioPlayerTemplate);

			// Get the play/pause button
			self.$playPause = self.$audioPlayerTemplate.find('.js-play-pause');

			// Get the bar played element
			self.$barPlayed = self.$audioPlayerTemplate.find('.js-bar-played');

			// Get the scrubber element
			self.$barScrubber = self.$audioPlayerTemplate.find(
				'.js-bar-scrubber'
			);

			// Get the current time element
			self.$currentTime = self.$audioPlayerTemplate.find(
				'.js-current-time'
			);

			// Get the roll back element
			self.$rollBack = self.$audioPlayerTemplate.find('.js-roll-back');

			// Get the roll forward element
			self.$rollForward = self.$audioPlayerTemplate.find(
				'.js-roll-forward'
			);

			// Get the player speed element
			self.$playerSpeed = self.$audioPlayerTemplate.find(
				'.js-player-speed'
			);

			// Get the scrub area
			self.$scrubArea = self.$audioPlayerTemplate.find('.js-scrub-area');

			// Get the scrub hover element
			self.$scrubHover = self.$audioPlayerTemplate.find(
				'.js-scrub-hover'
			);

			// Insert the template onto the DOM
			self.$audioPlayerTemplate.insertAfter(self.$el);

			// Put the audio element inside the template markup
			self.$audioPlayerTemplate.prepend(self.$el);

			// Hide the audio element
			self.$el.jsHide();

			// Run set mini on isMini model change
			self.model.onChange('isMini', function() {
				self.setMini();
			});

			// Check if mini is needed
			self.checkMini();

			// Check if mini is needed on any window resize
			$(window).on('resize.audioPlayer', function() {
				self.checkMini();
			});

			// Watch for global play action
			globalAudioModel.onChange('playAction', function() {
				self.model.set('isPlaying', false);
			});

			// Watch for change to isPlaying
			self.model.onChange('isPlaying', function(val) {
				if (val) {
					self.playAudio();
				} else {
					self.pauseAudio();
				}
			});

			// Play/pause click
			self.$playPause.on('click', function() {
				var shouldPlay = ! self.model.get('isPlaying');

				globalAudioModel.set(
					'playAction',
					globalAudioModel.get('playAction') + 1
				);

				self.model.set('isPlaying', shouldPlay);
			});

			// Watch for audio play
			self.el.addEventListener('play', function() {
				self.audioElPlayRespond();
			});

			// Watch for audio play once
			self.$el.on('play.firstPlay', function() {
				self.$el.off('play.firstPlay');
				self.setDuration();
			});

			// Watch for audio pause
			self.el.addEventListener('pause', function() {
				self.audioElPauseRespond();
			});

			// Watch for audio time update
			self.el.addEventListener('timeupdate', function() {
				self.audioTimeUpdate();
			});

			// Watch for click on roll back element
			self.$rollBack.on('click', function() {
				self.rollBack();
			});

			// Watch for click on roll forward element
			self.$rollForward.on('click', function() {
				self.rollForward();
			});

			// Watch for player speed click
			self.$playerSpeed.on('click', function() {
				self.playerSpeedUpdate();
			});

			// Scrub area mouse move
			self.$scrubArea.on('mousemove', function(e) {
				self.scrubAreaMouseMove(e);
			});

			// Scrub area mouse out
			self.$scrubArea.on('mouseout', function() {
				self.scrubAreaMouseOut();
			});

			// Scrub area click
			self.$scrubArea.on('click', function(e) {
				self.scrubAreaClick(e);
			});
		},

		/**
		 * Check if mini needed
		 */
		checkMini: function() {
			// Save a reference to the controller
			var self = this;

			// Update the model
			self.model.set('isMini', self.$audioPlayerTemplate.width() < 451);
		},

		/**
		 * Set mini
		 */
		setMini: function() {
			// Save a reference to the controller
			var self = this;

			if (self.model.get('isMini')) {
				// Add the mini class
				self.$audioPlayerTemplate.addClass(self.miniClass);
			} else {
				// Remove mini class
				self.$audioPlayerTemplate.removeClass(self.miniClass);
			}
		},

		/**
		 * Play audio
		 */
		playAudio: function() {
			this.el.play();
		},

		/**
		 * Play audio
		 */
		pauseAudio: function() {
			this.el.pause();
		},

		/**
		 * Respond to audio element play
		 */
		audioElPlayRespond: function() {
			// Save a reference to the controller
			var self = this;

			// Add and remove the appropriate classes
			self.$playPause.removeClass(self.playStoppedClass)
				.addClass(self.playPlayingClass);

			// Make sure volume is all the way up (users control volume of
			// their computer speakers, why would we want to control volume
			// here?)
			self.el.volume = 1;
		},

		/**
		 * Respond to audio element pause
		 */
		audioElPauseRespond: function() {
			// Save a reference to the controller
			var self = this;

			// Add and remove the appropriate classes
			self.$playPause.addClass(self.playStoppedClass)
				.removeClass(self.playPlayingClass);
		},

		/**
		 * Audio time update
		 */
		audioTimeUpdate: function() {
			// Save a reference to the controller
			var self = this;

			// Get the percentage played
			var per = (self.el.currentTime / self.el.duration) * 100;

			// Duration vars
			var time = new Date(null);
			var timeText = '';

			// Set the bar width to the appropriate percentage
			self.$barPlayed.css('width', per + '%');

			// Set the scrubber position to the appropriate percentage
			self.$barScrubber.css('left', per + '%');

			// Set current time seconds
			time.setSeconds(self.el.currentTime);

			// Get hours if aplicable
			if (time.getUTCHours()) {
				timeText += time.getUTCHours() + ':';

				if (time.getUTCMinutes() < 10) {
					timeText += '0';
				}
			}

			// Get minutes
			timeText += time.getUTCMinutes() + ':';

			// Get seconds
			if (time.getUTCSeconds() < 10) {
				timeText += '0';
			}
			timeText += time.getUTCSeconds();

			// Set the time
			self.$currentTime.text(timeText);
		},

		/**
		 * Set duration
		 */
		setDuration: function() {
			// Save a reference to the controller
			var self = this;

			// Duration vars
			var durTime;
			var durTimeText = '';

			// Check if we know the duration yet
			if (isNaN(self.el.duration)) {
				// Try again in a minute
				setTimeout(function() {
					self.setDuration();
				}, 50);

				// End processing of this function
				return;
			}

			// Get date object
			durTime = new Date(null);

			// Set the seconds
			durTime.setSeconds(self.el.duration);

			// Get hours if applicable
			if (durTime.getUTCHours()) {
				durTimeText += durTime.getUTCHours() + ':';

				if (durTime.getUTCMinutes() < 10) {
					durTimeText += '0';
				}
			}

			// Get minutes
			durTimeText += durTime.getUTCMinutes() + ':';

			// Get seconds
			if (durTime.getUTCSeconds() < 10) {
				durTimeText += '0';
			}
			durTimeText += durTime.getUTCSeconds();

			// Set the duration to the appropriate element
			self.$audioPlayerTemplate.find('.js-total-time').text(durTimeText);
		},

		/**
		 * Roll back
		 */
		rollBack: function() {
			// Save a reference to the controller
			var self = this;

			// Prepare var
			var skipBack;

			// Make sure audio is ready
			if (! self.el.readyState) {
				return;
			}

			// Set the skip back amount
			skipBack = self.el.currentTime - parseFloat(
				self.$rollBack.data('amount')
			);

			// Make sure it's not less than 0
			skipBack = skipBack > 0 ? skipBack : 0;

			// Set the time
			self.el.currentTime = skipBack;
		},

		/**
		 * Roll forward
		 */
		rollForward: function() {
			// Save a reference to the controller
			var self = this;

			// Prepare var
			var skipForward;

			// Make sure audio is ready
			if (! self.el.readyState) {
				return;
			}

			// Set the skip forward amount
			skipForward = self.el.currentTime + parseFloat(
				self.$rollForward.data('amount')
			);

			// Make sure it's not greater than the duration
			skipForward = skipForward < self.el.duration ?
				skipForward :
				self.el.duration;

			// Set the time
			self.el.currentTime = skipForward;
		},

		/**
		 * Player speed update
		 */
		playerSpeedUpdate: function() {
			// Save a reference to the controller
			var self = this;

			// Prepare var
			var rate;

			// Make sure audio is ready
			if (! self.el.readyState) {
				return;
			}

			// Start the playback rate over if it is 2 or greater
			if (self.el.playbackRate >= 2) {
				rate = 0.5;
			} else {
				rate = self.el.playbackRate + 0.25;
			}

			// Set the playback rate
			self.el.playbackRate = rate;

			// Show the playback speed indicator
			self.$playerSpeed.text(rate + 'x');
		},

		/**
		 * Scrub area mouse move
		 */
		scrubAreaMouseMove: function(e) {
			// Save a reference to the controller
			var self = this;

			// Get bar width
			var barWidth = self.$scrubArea.width();

			// Get percentage of the clicked area
			var per = (e.offsetX / barWidth) * 100;

			// Calculate the time based on percentage
			var seconds = (self.el.duration * per) / 100;

			// Time vars
			var time = new Date(null);
			var timeText = '';

			// Make sure audio is ready
			if (! self.el.readyState) {
				return;
			}

			// Set the scrubHover position
			self.$scrubHover.css('left', per + '%');

			// Set current time seconds
			time.setSeconds(seconds);

			// Get hours if aplicable
			if (time.getUTCHours()) {
				timeText += time.getUTCHours() + ':';

				if (time.getUTCMinutes() < 10) {
					timeText += '0';
				}
			}

			// Get minutes
			timeText += time.getUTCMinutes() + ':';

			// Get seconds
			if (time.getUTCSeconds() < 10) {
				timeText += '0';
			}
			timeText += time.getUTCSeconds();

			// Set the time to the appropriate element
			self.$scrubHover.text(timeText);

			// Set the left margin
			self.$scrubHover.css(
				'margin-left',
				'-' + ((self.$scrubHover.width() / 2) + 4) + 'px'
			);

			// Fade the element in if necesary
			if (! self.$scrubHover.is(':visible')) {
				self.$scrubHover.fadeIn(400);
			}
		},

		/**
		 * Scrub mouse out
		 */
		scrubAreaMouseOut: function() {
			// Hide the time indicator
			this.$scrubHover.fadeOut(400);
		},

		/**
		 * Scrub area click
		 */
		scrubAreaClick: function(e) {
			// Save a reference to the controller
			var self = this;

			// Get bar width
			var barWidth = self.$scrubArea.width();

			// Get percentage of the clicked area
			var per = (e.offsetX / barWidth) * 100;

			// Make sure audio is ready
			if (! self.el.readyState) {
				return;
			}

			// Calculate the time to set audio to based on percentage
			// and set the audio time
			self.el.currentTime = (self.el.duration * per) / 100;
		}
	});
})(window.FAB);
