(function(F) {
	'use strict';

	F.runOnPageRendered = function() {
		// Activate menu
		F.controller.construct('SiteNav', {
			el: '.site-header'
		});

		// Activate audio players
		$('.js-audio-player').each(function() {
			F.controller.construct('AudioPlayer', {
				el: this
			});
		});

		// Activate contact forms
		$('.js-contact-form').each(function() {
			F.controller.construct('ContactForm', {
				el: this
			});
		});
	};
})(window.FAB);
