(function(F, W) {
	'use strict';

	F.controller.make('SiteNav', {
		/**
		 * Mobile menu animation time
		 */
		mobileMenuAnimationTime: 150,

		/**
		 * Mode switch
		 */
		modeSwitch: 768,

		/**
		 * Mode
		 */
		mode: null,

		/**
		 * Nav list
		 */
		$navList: null,

		/**
		 * Model
		 */
		model: {
			mobileMenuIsActive: 'bool',
			mode: 'string'
		},

		/**
		 * Events
		 */
		events: {
			'click .js-mobile-menu-activator': function() {
				// Save a reference to the controller
				var self = this;

				// Set the menu active state
				self.model.set(
					'mobileMenuIsActive',
					! self.model.get('mobileMenuIsActive')
				);
			}
		},

		/**
		 * Init runs on controller construction
		 */
		init: function() {
			// Save a reference to the controller
			var self = this;

			// Set the nav list
			self.$navList = self.$el.find('.js-site-nav-list');

			// Watch for change to mobileMenuIsActive model property
			self.model.onChange('mobileMenuIsActive', function(val) {
				if (val) {
					self.openMobileMenu();
				} else {
					self.closeMobileMenu();
				}
			});

			// Watch for window resize
			W.addEventListener('resize', function() {
				// Determine when and how to set mode
				if (W.innerWidth >= self.modeSwitch && self.mode === 'mobile') {
					self.model.set('mode', 'desktop');
				} else if (W.innerWidth < self.modeSwitch && self.mode === 'desktop') {
					self.model.set('mode', 'mobile');
				}
			});

			// Watch for change to mode
			self.model.onChange('mode', function(val) {
				// Set the mode value
				self.mode = val;

				// Remove style from nav list
				self.$navList.attr('style', null);

				// Set mobile menu to inactive
				self.model.set('mobileMenuIsActive', false);
			});

			// Set mode
			self.model.set(
				'mode',
				W.innerWidth >= self.modeSwitch ? 'desktop' : 'mobile'
			);
		},

		/**
		 * Open mobile menu
		 */
		openMobileMenu: function() {
			// Save a reference to the controller
			var self = this;

			// Get the menu activator
			var $menuActivator = $('.js-mobile-menu-activator');

			// Get the active class
			var activeClass = $menuActivator.data('activeClass');

			// Set active class
			$menuActivator.addClass(activeClass);

			// Animate nav list open
			self.$navList.slideDown(self.mobileMenuAnimationTime);
		},

		/**
		 * Close mobile menu
		 */
		closeMobileMenu: function() {
			// Save a reference to the controller
			var self = this;

			// Get the menu activator
			var $menuActivator = $('.js-mobile-menu-activator');

			// Get the active class
			var activeClass = $menuActivator.data('activeClass');

			// Remove active class
			$menuActivator.removeClass(activeClass);

			// If we're in desktop mode, do not animate, remove the style
			if (self.model.get('mode') === 'desktop') {
				self.$navList.attr('style', null);

				// End processing
				return;
			}

			// Animate nav list close
			self.$navList.slideUp(self.mobileMenuAnimationTime);
		}
	});
})(window.FAB, window);
